___CSS_LOADER_EXPORT___.push([module.id, `:root {
    font-family:Arial, Helvetica, sans-serif;
    font-size: 62.5%;
    --ship-bg-colour: #43464B;
    box-sizing: border-box;
}

body {
    margin: 0px;
    padding: 0px;
    display: grid;
    grid-template-rows: 1fr 12fr 0.5fr;
    height: 100vh;
}

footer {
    text-shadow: 0px 2px 2px orange, 0px 0px 0.5rem red;
    box-shadow: inset 0px 0px 7px 9px #b6a9a978, 1px 0px 3rem 3px #db4315;
    background-color: #000000bf;
}

button {
    border-radius: 10px;
    border: 2px solid orangered;
    color: white;
    box-shadow: 0px 0px 1px 0.5px orange;
    background-color: var(--ship-bg-colour);
}

button:hover {
    background-color: black;
}

.game-container {
    display: flex;
    flex-direction: column;
    background-color: #262626;
    z-index: 1;
}

.game-display {
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-content: center;
    align-items: center;
    justify-items: center;
}

.game-board-container, .game-toolbar {
    display: flex;
    justify-content: center;
}

.game-board {
    display: grid;
    grid-template-columns: repeat(10, minmax(10px, 60px));
    justify-content: center;
    max-width: 90%;
    border: 1px solid black;
}

.game-cell {
    border: 1px solid black;
    background-color: #3987c9;
    aspect-ratio: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.pin {
    border: 1px solid black;
    background-color: black;
    width: 35%;
    height: 35%;
    border-radius: 20px;
}

#attacking .game-cell:not(.attacked):not(.ship-attacked){
    cursor: pointer;
}

header, dialog {
    font-size: 3.5rem;
    letter-spacing: 0.2rem;
    font-weight: bolder;
    text-shadow: 0px 2px 2px orange, 0px 0px 0.5rem red;
    box-shadow:  inset 0px 0px 7px 9px #b6a9a978, 1px 0px 3rem 3px #db4315;
    background-color: #000000bf;
    z-index: 2;
}

header {
    display: flex;
    align-items: center;
    justify-content: center;
}

.ship {
    background-color: var(--ship-bg-colour);
}

.game-cell.ship-attacked {
    background-color: var(--ship-bg-colour);
}


.attacked .pin {
    background-color: white;
    box-shadow: inset 0px 0px 5px 2px grey;
}

.game-cell.ship-attacked .pin {
    background-color: red;
    box-shadow: inset 0px 0px 5px 2px darkred;
}

#display-winner {
    font-size: 3rem;
    letter-spacing: 0.1rem;
}

#randomise-ships {
    font-size: 2rem;
    margin: 5px;
    padding: 5px 10px;
}

button:hover {
    cursor: pointer;
}

@media only screen and (max-width: 500px) {
    .game-display {
        grid-template-rows: 1fr 1fr;
        grid-template-columns: 1fr;
        row-gap: 10px;
    }
    .game-display {
        margin: 10px;
    }
}

@media only screen and (max-height: 750px) {
    .game-board {
        max-width: 70%;
    }
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,wCAAwC;IACxC,gBAAgB;IAChB,yBAAyB;IACzB,sBAAsB;AAC1B;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,aAAa;IACb,kCAAkC;IAClC,aAAa;AACjB;;AAEA;IACI,mDAAmD;IACnD,qEAAqE;IACrE,2BAA2B;AAC/B;;AAEA;IACI,mBAAmB;IACnB,2BAA2B;IAC3B,YAAY;IACZ,oCAAoC;IACpC,uCAAuC;AAC3C;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,yBAAyB;IACzB,UAAU;AACd;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,8BAA8B;IAC9B,qBAAqB;IACrB,mBAAmB;IACnB,qBAAqB;AACzB;;AAEA;IACI,aAAa;IACb,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,qDAAqD;IACrD,uBAAuB;IACvB,cAAc;IACd,uBAAuB;AAC3B;;AAEA;IACI,uBAAuB;IACvB,yBAAyB;IACzB,eAAe;IACf,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,uBAAuB;IACvB,uBAAuB;IACvB,UAAU;IACV,WAAW;IACX,mBAAmB;AACvB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,iBAAiB;IACjB,sBAAsB;IACtB,mBAAmB;IACnB,mDAAmD;IACnD,sEAAsE;IACtE,2BAA2B;IAC3B,UAAU;AACd;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,uCAAuC;AAC3C;;AAEA;IACI,uCAAuC;AAC3C;;;AAGA;IACI,uBAAuB;IACvB,sCAAsC;AAC1C;;AAEA;IACI,qBAAqB;IACrB,yCAAyC;AAC7C;;AAEA;IACI,eAAe;IACf,sBAAsB;AAC1B;;AAEA;IACI,eAAe;IACf,WAAW;IACX,iBAAiB;AACrB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI;QACI,2BAA2B;QAC3B,0BAA0B;QAC1B,aAAa;IACjB;IACA;QACI,YAAY;IAChB;AACJ;;AAEA;IACI;QACI,cAAc;IAClB;AACJ","sourcesContent":[":root {\n    font-family:Arial, Helvetica, sans-serif;\n    font-size: 62.5%;\n    --ship-bg-colour: #43464B;\n    box-sizing: border-box;\n}\n\nbody {\n    margin: 0px;\n    padding: 0px;\n    display: grid;\n    grid-template-rows: 1fr 12fr 0.5fr;\n    height: 100vh;\n}\n\nfooter {\n    text-shadow: 0px 2px 2px orange, 0px 0px 0.5rem red;\n    box-shadow: inset 0px 0px 7px 9px #b6a9a978, 1px 0px 3rem 3px #db4315;\n    background-color: #000000bf;\n}\n\nbutton {\n    border-radius: 10px;\n    border: 2px solid orangered;\n    color: white;\n    box-shadow: 0px 0px 1px 0.5px orange;\n    background-color: var(--ship-bg-colour);\n}\n\nbutton:hover {\n    background-color: black;\n}\n\n.game-container {\n    display: